header{
  background-color: rgb(5, 13, 17);
  color:rgb(255, 255, 255);
  
}

body{
  background-color: rgb(17, 28, 29);
  color:rgb(255, 255, 255);
}

.container{
  padding-top:8px;
  /* background-color: rgb(126, 174, 126); */
  width:80%;
  margin:auto;
  /* background-color: rgb(31, 54, 55); */
  padding-bottom:10px;

}

header .container{
  background-color: rgb(5, 13, 17);
  height:60px;
  display:flex;
  justify-content:space-between;
  align-items:center;
}

.navbar{
  list-style-type: none;
  height: inherit;
}

.navbar li{
  line-height:55px;
  display: inline-block;
  padding: 0 30px;
  height: inherit;
  text-align:center;
  transition: all 0.07s ease-in-out;
}

.content-options{
  /* margin: 6px 0; */
  text-align:end;
}

#margin-offset-left{
  margin-left: 8px;
}

#margin-offset-right{
  margin-right: 8px;
}

input[type=submit], input[type=button]{
  padding:8px 12px;
  border-radius:12px;
  background-color:rgb(9, 190, 121);
  color:white;
  font-weight:600;
}

input[type=submit]:hover, input[type=button]:hover{
  background-color:rgb(26, 162, 139);

}




.character{
  /* margin-bottom:4px; */
  text-align:center;
  color:white;
  border: 3x solid hotpink;
  /* aspect-ratio: 1/1.5; */
  height:100%;
  width:100%;
  
}

.character p{
  background-color: rgb(10, 17, 17);
  border-bottom-left-radius:10px;
  border-bottom-right-radius:10px;
  padding:12px;
}

.characters-grid{
  margin-top:10px;
  display:grid;
  grid-gap:.4em;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  /* justify-content:space-between;
  flex-wrap:wrap; */
}

.character_img{
  width:100%;
  aspect-ratio:1/1;
  object-fit:cover;
  
}

.cover{
  border-top-left-radius:10px;
  border-top-right-radius:10px;
  background-color: rgb(55, 55, 55);
}

.cover:hover{
  cursor:pointer;
  background-color: rgb(91, 91, 91);
}
/* 
.cover img{
  width:100%;
  height:100%;
  object-fit:cover;
} */

/* 
===================================================

  DETAIL 

=================================================== 
*/

.detail-heading{
  width:auto;
  display:flex;
  justify-content: space-between;
  align-items:center;
  height:28px;
  margin:12px 0;
}

.name{
  margin-top: 0px;
  line-height:0px;
  background-color: rgb(65, 102, 102);
}

.name{
  font-weight:600;
  font-size:28px;
}

.details-left{

  width:86%
}

.other-details{
  margin:16px 0;
}

.details-right{
  /* background-color:rgb(43, 129, 110); */
  width:34%;
  height:auto;
  text-align:center;
}

.details{
  display:flex;
}

.psuedo-header{
  font-weight:700;
  margin-right:8px;
}

.portrait{
  width:100%;
  aspect-ratio: 1 / 1;
  background-color: rgb(87, 87, 66);
}

.portrait img{
  object-fit: stretch;
}

.role input[type="button"], .abilities-table input[type="button"]{
  background-color: rgba(250, 235, 215, 0);
  font-size:12px;
  position:absolute;
  display:inline-block;
  padding: 1px 6px;
  border-radius: 40px;
  border: 2px solid rgb(88, 233, 184);
  color: rgb(88, 233, 184);
}

.inline-button-div{
  width:80px;
  height:19px;
  position:relative;
  display:inline-block;
  margin-left:8px;
  border: 2px solid rgba(255, 192, 203, 0);
}

.abilities-table h3{
  line-height:23px;
  margin-bottom:10px;
}

.role-description{
  margin:16px 0;
}

/* 
===================================================

  ABILITIES TABLE

=================================================== 
*/

#vanish{
  display:none;
}

table{
  margin-right: auto;
  width: 98%;
  height:100%;
  border-collapse: collapse;
}

table, th, td {
  border: 1px solid;
  font-size:14px;
}

th, td{
  padding:8px 10px 8px 12px;
}

#displayname{
  min-width:130px;
  font-size:18px;
}

.display-icon img{
  margin:auto;
  max-width:100%;
  aspect-ratio: 1/1;
}

#role{
  width:160px;
}

/* 
===================================================

  FORM

=================================================== 
*/

.row{
  display:flex;
  flex-direction:row;
  justify-content: space-between;
}

.formsubmit:disabled{
  background-color: rgb(103, 124, 115);
  cursor:default;
}

.formsubmit:disabled:hover{
  background-color: rgb(103, 124, 115);
  cursor:default;
}


.form-section{
  width:49.5%;
  background-color: rgb(34, 51, 53);
  padding:14px;
  border-radius:10px;
}

.valorant-background-border img{
  border-radius:10px;
}

h2{
  margin-bottom:12px;
  font-weight:600;
  font-size:28px;
}

.form-section form input[type=text], .form-section form #role {
  width:100%;
  padding:10px;
  margin:8px 0;
  border-radius:4px;
}

textarea{
  border-radius:4px;
  padding:10px;
  margin:8px 0;
  width:100%;
  resize:none;
}

.valorant-background-border{
  width:49.5%;
  /* background-color: yellow; */
  
}

.valorant-background-border img{
  width:100%;
  height:100%;
  /* aspect-ratio: 1/1; */
}

.fill{
  object-fit:cover;
}

/* 
===================================================

  NOT FOUND BLOCK

=================================================== 
*/

.not-found-block{
  /* border: 2px solid red; */
  width:90%;
  padding:20px 0 40px 40px;
  margin:auto;
  border-radius: 15px;
}

.not-found-block h2{
  font-size:40px;
  line-height:60px;
  
}

.return{
  padding: 10px 20px;
  border-radius:10px;
  background-color: rgb(24, 178, 101);
  color: rgb(255, 255, 255);
  font-weight:600;
}

.right-align{
  display:flex;
  justify-content: end;
  margin:20px 40px 0 0;
}

.return:hover{
  background-color: rgb(28, 154, 137);
}

/*

===================================================

  FOOTER

=================================================== 


*/

footer{
  padding:15px;
  width:100%;
  height:120px;
  display:flex;
  justify-content:space-between;
  border-radius:15px;
}


footer{
  background-color: rgb(25, 46, 46);
}

.footer-left p, .footer-middle p, .footer-right p{
  height:20px;
  width:inherit;
  color:white;
  display:flex;
  align-items:center;
}

.psuedo-header{
  font-weight:bold;
  margin-bottom:6px
}

.footer-middle p{
  justify-content:center;
}

.footer-right p{
  justify-content:flex-end;
}

svg{
  background-color: rgb(91, 204, 176);
  border-radius:50%;
  padding:.9em;
  margin: 0em 0.3em 0.3em 0em;
  width:3em;
  height:3em;
  font-size:1em;
  color:rgb(255, 255, 255);
}

svg:hover{
  background-color: rgb(58, 148, 158);
  cursor:pointer;
}

.social-media{
  height:4em;
  width:inherit;
  display:flex;
  align-items:center;
  justify-content:flex-start;
}

/*

===================================================

  404

=================================================== 


*/

.not-found-block{
  background-color: rgb(29, 48, 49);
  -webkit-box-shadow: 0 10px 6px -6px #000000;
     -moz-box-shadow: 0 10px 6px -6px #000000;
          box-shadow: 0 10px 6px -6px #000000;
}